/* ==============================================
PRODUCT DETAIL
============================================== */
#variant-selection {
  display: none !important;
}

.success {
  position: fixed;
  max-width: 600px;
  left: 50%;
  top: 80px;
  padding: 10px 20px;
  background-color: $color-link;
  color: white;
  border-radius: 4px;
  transform: translateX(-50%);
}

.view-product {
  .discount-msg {
    padding: 10px 15px;
    margin-bottom: 20px;
    border: 2px solid $color-primary;
    border-radius: 6px;
    background-color: rgba(white, .5);

    h3 {
      font-size: 14px;
      text-transform: uppercase;
      margin: 0;
    }

    p {
      margin-bottom: 0;
      font-size: 16px;
      line-height: 1.25;
    }
  }

  .product-details {
    &__info {
      h1 {
        font-family: $font-body;
        font-weight: 700;
      }
    }

    &__price {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 10px 0;
      border-top: 1px solid $color-border;
      border-bottom: 1px solid $color-border;
      font-size: 24px;
      font-weight: bold;
    }

    hr {
      display: none;
      margin: 20px 0;
    }
  }

  .product {
    &__main-info {
      display: flex;
      flex-direction: column;
    }

    &__fields {
      display: flex;
      align-items: center;
      margin: 0 0 20px 0;
      
      .quantity {
        flex: 0 1 auto;
        max-width: 80px;
        margin: 0 10px;
      }

      .button {
        margin-bottom: 0 !important;
      }
  
      h5 {
        font-family: $font-body;
        font-weight: 700;
      }
    }

    &__variants {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &__variant {
      flex: 1 1 auto;
      width: calc(33% - 5px);
      margin: 0 5px;
      padding: 10px;
      background-color: white;
      box-shadow: 0 8px 12px -4px rgba(black, .25);
      border-radius: 10px;
      text-align: center;
      transition: all .15s $ease-out-quad;
      cursor: pointer;

      &:first-child {
        margin-left: 0;
      }
      
      &:last-child {
        margin-right: 0;
      }

      h4 {
        margin: 0;
        font-family: $font-body;
        font-weight: 700;
        font-size: 16px;
      }

      &.active {
        background-image: linear-gradient(to bottom,#fafafa,#f0f0f0);
        box-shadow: inset 0 5px 5px rgba(1,0,0,.25), inset 0 0 0 4px $color-link, 0 0 0 1px #aaa;
      }
    }

    &__photos {
      display: flex;
      margin-top: 10px;

      &--justify {
        justify-content: space-between;
      }
    }

    &__photo {
      width: 100px;
      transition: all .15s $ease-out-quad;
      border-radius: 4px;
      overflow: hidden;

      > img {
        display: block;
      }

      &.active {
        position: relative;
        z-index: 5;
        box-shadow: 0 0 0 5px $color-link;
      }
    }

    &__nostock {
      background-color: $color-error-light;
      border: 1px solid darken($color-error-light, 6%);
      padding: 20px;
      margin-bottom: 20px;
      border-radius: 6px;

      p {
        margin-bottom: 0;
      }
    }
  }

  .product-details {
    margin-top: 0;

    &__photo > img {
      overflow: hidden;
      border-radius: 4px;;
    }
  }
}


/* ==============================================
RESPONSIVE - TABLET
============================================== */
@media (min-width: $mq-sm) and (max-width: $mq-md) {
  .product-details {
    padding-top: 0;
    flex-direction: column;

    &__photo {
      width: 50%;
      margin: 0 auto;
      padding-top: 0;
    }

    &__info {
      width: 100%;
      margin: 0;
      padding-top: 0;

      h1 {
        text-align: center;
      }
    }
  }

  .product {
    &__main-info {
      flex-direction: column-reverse !important;
    }
  }

  &__fields {
    width: 100%;
  }

  .add-to-cart {
    display: flex;
    align-items: flex-end;
    justify-content: center;

    .buttons {
      margin-left: 20px;
    }
  }
}


/* ==============================================
RESPONSIVE - MOBILE
============================================== */
@media (max-width: $mq-md) {
  .product-details {
    padding: 20px 20px 0 20px;
    flex-direction: column;

    &__photo {
      width: 80%;
      margin: 0 auto;
      padding-top: 0;
    }

    &__info {
      width: 100%;
      margin: 0;
      padding-top: 0;
      padding: 10px 20px;
      background: rgba(white, .4);
      border-radius: 6px;
      border-top: 1px solid rgba(white, .75);
      box-shadow: 0 20px 20px -5px rgba(black, .15);
    }

    hr {
      display: block !important;
    }
  }

  .product {
    &__intro {
      p {
        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    &__fields {
      justify-content: center;

      .field {
        &--variant {
          flex: 1 0 auto;
        }
      }

      select {
        width: 100%;
      }
    }

    &__main-info {
      flex-direction: column-reverse !important;
    }
  }
}
